.yqjkxy-wrapper {}

.mar-box {
    padding: 0 0.75rem;
}


/*头部*/

.header-wrapper {
    width: 100%;
    height: 2.225rem;
    background: url(../img/system_header_bg1.png) no-repeat center;
    /*background-size: 100% auto;*/
    overflow: hidden;
    .header-title {
        font-size: 1.05rem;
        color: #fefefe;
        height: 2.225rem;
        line-height: 2.225rem;
        text-align: center;
        // position: absolute;
        // left: 0;
        // right: 0;
        letter-spacing: 0.125rem;
        text-shadow: 0.25rem 0.25rem 0.125rem #081137;
        font-family: "fzydthjw";
    }
    .top-btn {
        font-size: 0.5rem;
        margin-top: 0.5rem;
        color: #fff;
		position: absolute;
		top: 0;
    }
    .time-show {
        // float: left;
        span {
            margin-left: 0.375rem;
        }
    }
    .home-btn {
		position: absolute;
		right:0.75rem;
        // float: right;
        i {
            width: 0.55rem;
            height: 0.55rem;
            display: inline-block;
            vertical-align: middle;
            margin-right: 0.25rem;
        }
        span {
            cursor: pointer;
            margin-left: 0.5rem;
        }
        .icon_quit {
            background: url(../img/system_header_quit.png) no-repeat center;
            background-size: 100% 100%;
        }
        .icon_wholeScreen {
            background: url(../img/system_header_wholeScreen.png) no-repeat center;
            background-size: 100% 100%;
        }
    }
}

.main-wrapper {
    margin-top: 0.5rem;
    .title-head {
        color: #fff;
        font-size: 0.45rem;
        .title-line {
            height: 0.5rem;
            width: 100%;
            background: url(../img/title-line.png) no-repeat center;
            background-size: 100% 100%;
        }
    }
}

.material-type {
    text-align: center;
    margin-top: 0.9rem;
    .item_logo {
        height: 2.25rem;
    }
    .type-count {
        font-size: 0.75rem;
        color: #44f7d6;
        font-family: "degital";
        span {
            font-size: 0.35rem;
        }
    }
    .type-name {
        font-size: 0.4rem;
        color: #fff;
    }
}


/*应急储备情况*/

.emergency-reserve-info {
    float: right;
    width: 12.25rem;
    height: 23.175rem;
    padding: 0.25rem 0.5rem;
    background: url(../img/box-1.png) no-repeat center;
    background-size: 100% 100%;
    box-sizing: border-box;
    .material-dec {
        .box-item {
            background: url(../img/box-line.png);
            background-size: 100% 100%;
            width: 100%;
            height: 1.475rem;
            line-height: 1.475rem;
            margin-top: 0.6rem;
        }
        .item_name {
            width: 50%;
            float: left;
            text-align: center;
            color: #fff;
            font-size: 0.4rem;
        }
        .item_number {
            width: 45%;
            float: right;
            text-align: right;
            color: #44f7d6;
            font-size: 0.9rem;
            margin-right: 0.5rem;
            font-family: "degital";
            span {
                font-size: 0.4rem;
            }
        }
    }
}


/*中间内容*/

.content-box {
    float: right;
    margin-right: 0.5rem;
    width: 21.2rem;
    position: relative;
}

.content-part1 {
    .piece {
        position: relative;
        background: url(../img/system_content_sider5.png) no-repeat center;
        background-size: 100% 100%;
        height: 2.8rem;
        width: 3.325rem;
        padding: .4rem 0 0 0;
        box-sizing: border-box;
        .piece-title {
            font-size: 0.4rem;
            color: #fff;
            padding: 0 0 0 .2rem;
        }
        .title-line-2 {
            background: url(../img/title-line-2.png)no-repeat center;
            background-size: 100% 100%;
            width: 0.5rem;
            height: 0.35rem;
            display: inline-block;
        }
        .item-countbg,
        .item-count {
            position: absolute;
            top: 1.25rem;
            width: 100%;
            text-align: right;
            letter-spacing: 0.125rem;
            font-family: "degital";
            font-size: 1.15rem;

        }
		.item-count.six{
			font-size: 0.8rem;
			top: 1.5rem;
			letter-spacing: 0.1rem;
		}
        .item-countbg {
            z-index: 8;
            color: #3c5492;
        }
        .item-count {
            z-index: 10;
            text-align: right;
        }
    }
}

.green-color {
    color: #44f7d6;
}

.yellow-color {
    color: #f9d824;
}

.orange-color {
    color: #ff843a;
}

.red-color {
    color: #ff4e4e;
}

.white-color {
    color: #fff;
}


/*第二屏*/

.area-li {
    .first-box {
        height: 1.2rem;
        line-height: 1.2rem;
        font-size: 0.45rem;
        border-bottom: 1px solid #2894e5;
    }
    .col1 {
        width: 26%;
        color: #e0f2ff;
    }
    .col2 {
        width: 17%;
    }
    .second-box {
        height: 100%;
        display: none;
        position: relative;
        /*z-index: 9;*/
        font-size: 0.35rem;
        .sec-dt {
            height: 1.05rem;
            line-height: 1.05rem;
            font-size: 0.35rem;
            border-bottom: 1px solid #164c85;
        }
    }
}

.area-li.active {
    .second-box {
        display: block;
    }
}
.areaName{
	position: absolute;
	color: #fff;
	margin-top: 0.5rem;
    margin-left: 0.25rem;
    font-size: 0.5rem;
}
.areaName-back{
	text-align: right;
    color: #fff;
    margin-top: 0.5rem;
    margin-right: 0.25rem;
    font-size: 0.5rem;
    display: none;
	cursor: pointer;
	position: absolute;
    right: 0;
	i{ 

		display: inline-block;
		background: url(../img/back-btn.png) no-repeat center;
        background-size: 100% 100%;
		width: 0.8rem;
		height: 0.8rem;
		vertical-align: text-bottom;
	}
}

/*轮播*/
.content {
	margin: 0 auto;
	width: 46.5rem;
	height: 23.175rem;
	position: relative;
	.related-ctc {
        margin: 0 auto;
        width: 46.5rem;
        height: 23.175rem;
        position: relative;
	}
	.nav-list{
		color: #fff;
		position: absolute;
		top:50%;
		right:-0.1rem;
		margin-top: -0.5rem;
		li{
			width: 0.25rem;
			height: 0.25rem;
			border:0.05rem solid #0cb9ff;
			border-radius:50%;
			margin:0.25rem 0;
			cursor: pointer;
		}
		.on{
			background: #0cb9ff;
		}
	}
}
/*修改滚动条样式*/
div::-webkit-scrollbar{
  width:4px;
  height:6px;
}
div::-webkit-scrollbar-track{
  background: rgb(239, 239, 239);
  border-radius:2px;
}
div::-webkit-scrollbar-thumb{
  background: #bfbfbf;
  border-radius:10px;
}
div::-webkit-scrollbar-thumb:hover{
  background: #333;
}
div::-webkit-scrollbar-corner{
  background: #179a16;
}