body {
    margin: 0;
}

.yqjkxt {
    width: 1920px;
    height: 1080px;
    margin: 0 auto;
    background: url(../img/system_bg.png) no-repeat center;
    background-size: 100% 100%;
    color: #fefefe;
    font-weight: 700;
}

.yqjkxt i {
    position: relative;
    display: inline-block;
}

.yqjkxt i.space {
    width: 10px;
}

.yqjkxt .header {
    width: 100%;
    height: 89px;
    background: url(../img/system_header_bg.png) no-repeat center;
    background-size: 100% auto;
    overflow: hidden;
}

.yqjkxt .header .time {
    float: left;
    margin-left: 32px;
    margin-top: 18px;
    line-height: 20px;
}

.yqjkxt .header .control {
    float: right;
    margin-right: 32px;
    margin-top: 18px;
    line-height: 20px;
    overflow: hidden;
}

.yqjkxt .btn_control {
    float: right;
    cursor: pointer;
    margin-left: 22px;
}

.yqjkxt .btn_control i {
    width: 22px;
    height: 22px;
    top: 5px;
    margin-right: 6px;
}

.yqjkxt .btn_control .icon_quit {
    background: url(../img/system_header_quit.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .btn_control .icon_wholeScreen {
    background: url(../img/system_header_wholeScreen.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .btn_control .icon_quitScreen {
    background: url(../img/system_header_quitScreen.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .content {
    overflow: hidden;
    padding: 26px 28px;
    padding-bottom: 0;
    /* font-family: "degital";
    font-size: 28px;     letter-spacing: .1em;
*/
}

/* 左侧样式 */

.yqjkxt .content .content_left {
    float: left;
    width: 452px;
    height: 920px;
    padding: 10px 20px;
    background: url(../img/system_content_sider1.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .content .content_header,
.yqjkxt .cc_part3 .item_right {
    position: relative;
}

.yqjkxt .content .content_header span {
    font-weight: 900;
    /* position: absolute; */
    left: 0;
    top: 0;
}

.yqjkxt .content .content_left .light {
    position: relative;
    top: -16px;
    width: 100%;
    height: 52px;
    background: url(../img/system_content_light1.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .cl_part1,
.yqjkxt .cl_part3,
.yqjkxt .cr_part2 {
    position: relative;
    overflow: hidden;
    margin-bottom: 44px;
}

.yqjkxt .cr_part2 {
    margin-bottom: 0;
}

.yqjkxt .cl_part3 {
    margin-bottom: 36px;
}

.yqjkxt .cl_part1 .item,
.yqjkxt .cl_part3 .item,
.yqjkxt .cr_part2 .cr_content .item {
    float: left;
    width: 33%;
    text-align: center;
}

.yqjkxt .cr_part2 .cr_content .item {
    margin-bottom: 20px;
}

.yqjkxt .cl_part1 .item img,
.yqjkxt .cl_part3 .item img {
    height: 90px;
}

.yqjkxt .cr_part2 .cr_content .item img {
    width: 90px;
    height: 65px;
    margin-bottom: 16px;
}

.yqjkxt .cl_part1 .item span,
.yqjkxt .cl_part3 .item span,
.yqjkxt .cr_part2 .cr_content .item span {
    position: relative;
    display: block;
}

.yqjkxt .cl_part1 .item_number,
.yqjkxt .cl_part3 .item_number,
.yqjkxt .cr_part2 .item_number {
    font-size: 22px;
    color: #44f7d6;
    font-family: "degital";
    font-size: 28px;
    letter-spacing: .1em;
}

.yqjkxt .cl_part1 .item_name,
.yqjkxt .cl_part3 .item_name,
.yqjkxt .cr_part2 .item_name {
    font-size: 14px;
    line-height: 22px;
    font-weight: 700;
}

.yqjkxt .cl_part3 .item_name,
.yqjkxt .cr_part2 .item_name {
    margin-top: 16px;
}

.yqjkxt .cl_part2 {
    position: relative;
    margin-bottom: 44px;
}

.yqjkxt .cl_part2 .item {
    background: url(../img/system_content_sider2.png) no-repeat center center;
    background-size: 100% 100%;
    margin: 25px 0;
    overflow: hidden;
}

.yqjkxt .cl_part2 .item_name {
    float: left;
    width: 40%;
    text-align: center;
    line-height: 60px;
}

.yqjkxt .cl_part2 .item_number {
    float: right;
    width: 35%;
    padding-right: 5%;
    text-align: right;
    line-height: 60px;
    color: #44f7d6;
    font-family: "degital";
    font-size: 28px;
    letter-spacing: .1em;
    font-size: 28px;
}

/* 右侧样式 */
.yqjkxt .content_right {
    float: right;
    width: 492px;
}

.yqjkxt .content_right .cr_part1 {
    position: relative;
    overflow: hidden;
    padding: 12px 16px;
    margin-bottom: 20px;
    background: url(../img/system_content_sider9.png) no-repeat center center;
    background-size: 100% 100%;
}

.yqjkxt .cr_part1 .content_header,
.yqjkxt .cr_part2 .content_header {
    position: relative;
}

.yqjkxt .cr_part1 .content_header span,
.yqjkxt .cr_part2 .content_header span {
    font-weight: 900;
    left: 0;
    top: 0;
}

.yqjkxt .cr_part1 .light,
.yqjkxt .cr_part2 .light {
    position: relative;
    top: -16px;
    width: 100%;
    height: 52px;
    background: url(../img/system_content_light3.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .part_title {
    line-height: 40px;
    padding: 10px 20px;
    margin-top: -34px;
    background: url(../img/system_content_item_bg.png) no-repeat center;
    background-size: 100% 100%;
    overflow: hidden;
}

.yqjkxt .part_desc {
    float: left;
}

.yqjkxt .part_number {
    float: right;
    color: #fba721;
    font-family: "degital";
    font-size: 28px;
    letter-spacing: .1em;
}

.yqjkxt .part_desc i {
    top: 8px;
    width: 42px;
    height: 30px;
    margin-right: 2px;
    background: url(../img/system_content_icon2.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .part_content {
    overflow: hidden;
}

.yqjkxt .part_content .item {
    float: left;
    width: 195px;
    height: 122px;
    padding: 5px 12px;
    margin-top: 16px;
    margin-right: 20px;
    background: url(../img/system_content_sider7.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .part_content .item:nth-child(2),
.yqjkxt .part_content .item:nth-child(4) {
    margin-right: 0;
}

.yqjkxt .part_content .item_name {
    display: block;
}

.yqjkxt .part_content .item_data {
    position: relative;
    display: inline-block;
    width: 70px;
    height: 80px;
    margin-top: 16px;
    text-align: center;
    overflow: hidden;
    font-size: 12px;
}

.yqjkxt .part_content .item_data span {
    line-height: 36px;
    font-size: 16px;
    margin-right: 6px;
    color: #44f7d6;
    font-family: "degital";
    font-size: 21px;
    letter-spacing: .1em;
}

.yqjkxt .part_content .item_total {
    float: right;
}

.yqjkxt .part_content .item_total span {
    color: #fba721;
}

.yqjkxt .part_content span.dataType {
    display: block;
    margin-right: 0;
    color: #fefefe;
}

.yqjkxt .content_right .cr_part2 {
    position: relative;
    overflow: hidden;
    padding: 12px 16px;
    background: url(../img/system_content_sider9.png) no-repeat center center;
    background-size: 100% 100%;
    overflow: hidden;
}

.yqjkxt .content_right .cr_part2 .cr_tab {
    float: right;
    border-radius: 2px;
    margin-top: -22px;
    overflow: hidden;
}

.yqjkxt .content_right .cr_part2 .tab_item {
    float: left;
    line-height: 24px;
    padding: 0 6px;
    border: 1px solid #2894e5;
    border-right: none;
    font-size: 14px;
    cursor: pointer;
}

.yqjkxt .cr_part2 .tab_item:last-child {
    border: 1px solid #2894e5;
    border-right: 1px solid #2894e5;
}

.yqjkxt .cr_part2 .tab_active {
    background: #2894e5;
}

.yqjkxt .cr_part2 .cr_content {
    display: none;
    margin-top: 40px;
    overflow: hidden;
}

.yqjkxt .cr_part2 .cr_content .item {
    float: left;
}

.yqjkxt .cr_part2 .crc_active {
    display: block;
}

/* 中间样式 */

.yqjkxt .content_center {
    margin: 0px 514px;
}

.yqjkxt .cc_part1 {
    margin-bottom: 40px;
    overflow: hidden;
}

.yqjkxt .cc_part1 .item {
    position: relative;
    float: left;
    width: 17%;
    height: 88px;
    padding: 10px 6px 10px 12px;
    margin-right: 1%;
    background: url(../img/system_content_sider5.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .cc_part1 .item:last-child {
    margin-right: 0;
}

.yqjkxt .cc_part1 .item_name i {
    top: 6px;
    width: 38px;
    height: 28px;
    background: url(../img/system_content_icon1.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .cc_part1 .item_name span {
    line-height: 30px;
}

.yqjkxt .cc_part1 .item_number {
    position: absolute;
    bottom: 20px;
    width: 135px;
    text-align: right;
    font-size: 26px;
    letter-spacing: 5px;
    z-index: 10;
    font-family: "degital";
    font-size: 28px;
}

.yqjkxt .cc_part1 .item_numberbg {
    position: absolute;
    bottom: 20px;
    width: 135px;
    text-align: right;
    font-size: 26px;
    letter-spacing: 5px;
    color: #000;
    z-index: 8;
    font-family: "degital";
    font-size: 28px;
}

.yqjkxt .cc_part1 .jiuzhen {
    color: #227c6c;
}

.yqjkxt .cc_part1 .yisi {
    color: #f9d824;
}

.yqjkxt .cc_part1 .quezhen {
    color: #fba721;
}
.yqjkxt .cc_part1 .chuyuan {
    color: #44f7d6;
}
.yqjkxt .cc_part1 .siwang {
    color: #ff4e4e;
}

.yqjkxt .cc_part2 {
    height: 490px;
    margin-bottom: 48px;
}

.yqjkxt .cc_part3 {
    overflow: hidden;
}

.yqjkxt .cc_part3 .item_left {
    float: left;
    width: 484px;
    height: 234px;
    padding: 10px 20px;
    background: url(../img/system_content_sider3.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .cc_part3 .content_header {
    position: relative;
}

.yqjkxt .cc_part3 .content_header span {
    font-weight: 900;
    left: 0;
    top: 0;
}

.yqjkxt .cc_part3 .light {
    position: relative;
    top: -16px;
    width: 100%;
    height: 52px;
    background: url(../img/system_content_light2.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .cc_part3 .subItem {
    padding: 16px 30px;
    background: url(../img/system_content_sider10.png) no-repeat center;
    background-size: 100% 100%;
    /* font-size: 10px; */
}

.yqjkxt .cc_part3 .subItem_condition {
    position: relative;
    display: inline-block;
    text-align: center;
    font-size: 8px;
    margin-left: 30px;
}

.yqjkxt .cc_part3 .ysbl,
.yqjkxt .cc_part3 .ysblywry {
    width: 28px;
    height: 30px;
    margin-right: 10px;
}

.yqjkxt .cc_part3 .ysbl {
    background: url(../img/system_content_icon3.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .cc_part3 .ysblywry {
    background: url(../img/system_content_icon4.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .cc_part3 .subItem_name {
    position: relative;
    display: inline-block;
    width: 90px;
    height: 40px;
    line-height: 40px;
    text-align: center;
}

.yqjkxt .cc_part3 .subItem_condition span {
    margin-right: 6px;
    font-size: 16px;
    font-family: "degital";
    letter-spacing: .1em;
}

.yqjkxt .cc_part3 .subItem_today span {
    color: #00d9ff;
}

.yqjkxt .cc_part3 .subItem_total span {
    color: #fba721;
}

.yqjkxt .cc_part3 .subItem_add span {
    color: #21fb2c;
}

.yqjkxt .cc_part3 .subItem_condition span.dataType {
    display: block;
    margin-right: 0;
    color: #fefefe;
}

.yqjkxt .cc_part3 .item_right {
    float: right;
    width: 256px;
    height: 234px;
    padding: 10px 20px;
    background: url(../img/system_content_sider4.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .item_right .jcys_bg{
    height: 136px;
    width:136px;
    position: absolute;
    bottom:60px;
    left:80px;
    background: url(../img/waterPic_bg.png) no-repeat center;
    background-size: 100% 100%;
}

.yqjkxt .item_right #jcys{
    height: 250px;
    position: relative;
    bottom:85px;
}