    .second-screen .content-left {
        float: left;
        width: 33.75rem;
        height: 5rem;
    }
    
    .second-screen .content-right {
        float: right;
        width: 12.25rem;
        height: 23.05rem;
		overflow-y: auto;
        background: url(../img/second_screen_right_bg.png) no-repeat center;
        background-size: 100% 100%;
        padding: 0.25rem 0.5rem;
        box-sizing: border-box;
    }
    
    .second-screen .content-left .chart-wrap {
        height: 11.275rem;
        background: url(../img/second_screen_chart_bg.png) no-repeat center;
        background-size: 100% 100%;
        margin-bottom: .5rem;
    }
    
    .subtitle {
        position: relative;
        height: 1.25rem;
        line-height: 1.25rem;
        padding: 0 0 0 .5rem;
        font-size: .45rem;
        color: #fff;
        font-weight: bold;
        .light1 {
            position: absolute;
            top: .5rem;
            display: block;
            width: 13.875rem;
            height: 1.325rem;
            background: url(../img/system_content_light1.png) no-repeat center;
            background-size: 100% 100%;
        }
        .light2 {
            position: absolute;
            top: .5rem;
            display: block;
            width: 12.25rem;
            height: 1.325rem;
            background: url(../img/system_content_light2.png) no-repeat center;
            background-size: 100% 100%;
        }
    }
    
    .second-screen #chart_bar1,
    .second-screen #chart_bar2 {
        width: 100%;
        height: 9.4rem;
    }
    
    .second-screen .table-warp {
        font-size: 0;
    }
    
    .second-screen .table-head {
        height: 1rem;
        line-height: 1rem;
        color: #fff;
        border-bottom: 2px solid #2894e5;
    }
    
    .second-screen .row-wrap {
        height: 1rem;
        line-height: 1rem;
        color: #fff;
        border-bottom: 1px solid #2894e5;
    }
    
    .second-screen .row-wrap2 {
        height: 1rem;
        line-height: 1rem;
        color: #fff;
        border-top: 1px solid #1c548a;
    }
    .second-screen .city-state-data .city {
        position: relative;
        line-height: 1rem;
        color: #fff;
        border-bottom: 1px solid #2894e5;
    }
    
    .second-screen .city-state-data .active .triangle-icon {
        position: absolute;
        top: 50%;
        left: .2rem;
        transform: translateY(-50%);
        display: inline-block;
        width: .35rem;
        height: .2rem;
        background: url(../img/triangle-icon.png) no-repeat center;
        background-size: 100% 100%;
    }
    
    .second-screen .county {
        display: none;
        width: 100%;
    }
    
    .second-screen .show {
        display: block;
    }
    
    .second-screen .triangle-icon {
        position: absolute;
        top: 50%;
