    /* 首页中间样式 */
    
    .content-part3 {
        width: 21.225rem;
        height: 7.45rem;
        background: url(../img/home-center-bottom.png) no-repeat center;
        background-size: 100% 100%;
        #chart_line {
            width: 100%;
            height: 6rem;
        }
    }
    
    .left-part {
        float: left;
        width: 12.25rem;
        .left-part1 {
            height: 4.775rem;
            padding: 0.25rem 0.5rem;
            box-sizing: border-box;
            background: url(../img/box-left-1.png) no-repeat center;
            background-size: 100% 100%;
        }
        .left-part2 {
            height: 12.05rem;
            padding: 0.25rem 0.5rem;
            box-sizing: border-box;
            background: url(../img/box-left-2.png) no-repeat center;
            background-size: 100% 100%;
            .material-type {
                margin-top: .5rem;
            }
        }
        .left-part3 {
            height: 5.125rem;
            margin: .6rem 0;
            padding: 0.25rem 0.5rem;
            box-sizing: border-box;
            background: url(../img/box-left-3.png) no-repeat center;
            background-size: 100% 100%;
        }
        .material-type .type-name {
            width: 2.85rem;
            font-size: 0.4rem;
            color: #fff;
            margin: 0 auto;
        }
        .flex-wrap {
            display: flex;
            flex-wrap: nowrap;
            justify-content: space-between;
            .item {
                .type-count {
                    font-size: 1.3rem;
                    font-family: "degital";
                    span {
                        font-size: .4rem;
                        color: #FFF;
                    }
                }
                .type-name {
                    font-size: .45rem;
                    color: #fff;
                    font-weight: bold;
                }
            }
        }
        .flex-wrap1 {
            padding: 0 1.7rem;
        }
        .flex-wrap2 {
            padding: 0 .7rem;
            .item {
                width: 2.8rem;
                margin-right: .25rem;
            }
        }
        .title-head {
            position: relative;
        }
        .cr_tab {
            position: absolute;
            right: 0;
            top: 0.55rem;
            border-radius: 2px;
            margin-top: -0.55rem;
            overflow: hidden;
        }
        .tab_item {
            float: left;
            line-height: .6rem;
            padding: 0 6px;
            border: 1px solid #2894e5;
            border-right: none;
            font-size: .35rem;
            cursor: pointer;
        }
        .tab_item:last-child {
            border: 1px solid #2894e5;
            border-right: 1px solid #2894e5;
        }
        .tab_active {
            background: #2894e5;
        }
    }
    
    #main_gansu {
        width: 17rem;
        height: 12rem;
        margin: .4rem 0 .5rem 2rem;
    }
    
